@font-face {
  font-family: Stardew Valley Stonks;
  src: url("Stardew Valley Stonks.822eb553.otf");
}

.back:before, .column-sort > button:after, .collapse-arrow:before, details > summary:before {
  content: "";
  width: 1em;
  height: 1em;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
}

.back:before {
  background-image: url("Arrow Left.4a1e75f6.png");
}

.collapse-arrow[aria-pressed="false"]:before, details > summary:before {
  background-image: url("Arrow Right.cb6b7e58.png");
}

.column-sort[aria-sort="ascending"] > button:after, .column-sort:not([aria-sort]) > button:hover:after {
  background-image: url("Arrow Up.7b32ea7a.png");
}

.column-sort[aria-sort="descending"] > button:after, .collapse-arrow[aria-pressed="true"]:before, details[open] > summary:before {
  background-image: url("Arrow Down.183d95dd.png");
}

.winter, .fall, .summer, .spring {
