}

.winter {
  content: url("Winter.038e7489.png");
}

.icon, .quality, .botanist, .gatherer, .agriculturist, .artisan, .tiller, .juice, .wine, .pickles, .jam {
  width: 1.5em;
  height: 1.5em;
}

.icon-processor {
  width: 1.25em;
}

.icon-text {
