  margin-right: .25em;
}

.jam {
  content: url("Jam.ea13e884.png");
}

.pickles {
  content: url("Pickles.fcd6cf1e.png");
}

.wine {
  content: url("Wine.39d35c00.png");
}

.juice {
  content: url("Juice.862e0c79.png");
}

.tiller {
  content: url("Tiller.5e16e81f.png");
}

.artisan {
  content: url("Artisan.f6bc5f14.png");
}

.agriculturist {
  content: url("Agriculturist.83c17e6d.png");
}

.gatherer {
  content: url("Gatherer.8dcfbeb0.png");
}

.botanist {
  content: url("Botanist.6024f180.png");
}

.foraging, .farming {
  width: 1.25em;
  height: 1.25em;
}

.farming {
  content: url("Farming.f31efd96.png");
}

.foraging {
  content: url("Foraging.2a8c4a0c.png");
}

.iridium {
  --color: purple;
  --img: url("Iridium.c27a7f8a.png");
}

.gold {
  --color: gold;
  --img: url("Gold.52c59afe.png");
}

.silver {
  --color: silver;
  --img: url("Silver.e30923de.png");
}

.normal {
  --color: white;
  --img: url("Normal.2db4444f.png");
}

.label-hidden:after, .label-text:after, dt:after {
  content: ":";
  font-weight: 600;
}

.ok, .cancel, .edit, [role="tab"], .back, .column-sort > button, .collapse-arrow {
  box-shadow: none;
  color: #000;
  background: none;
  border: none;
  outline: none;
  padding: 0;
}

dialog, [role="tab"], [role="tabpanel"], footer > span {
  image-rendering: pixelated;
  border-image-source: url("Border Box.062adf39.png");
  border-image-slice: 16 fill;
  border-image-width: 1rem;
  border-image-outset: .75rem;
  margin: .75rem;
}

dialog > *, [role="tab"] > *, [role="tabpanel"] > *, footer > span > * {
  image-rendering: initial;
}

.checkbox > input:focus + img, .select:focus-within, .file-input > div:first-child > label:focus-within, .professions > div > label:focus-within, .column-sort > button:focus, .collapse-arrow:focus:before, :focus {
  outline: var(--border-thin) solid #d83700;
  outline-offset: var(--border-thin);
}

input[type="range"]:hover, [role="tab"]:hover, .input-box:hover, .button:hover, .file-input > div:first-child > label:hover {
  filter: brightness(1.15);
}

.ok:hover, .cancel:hover, .edit:hover, .checkbox:hover > input + img, .back:hover:before, .collapse-arrow:hover:before, details > summary:hover:before {
  transform: scale(1.1);
}

:root {
  font-family: Stardew Valley Stonks, Nunito, sans;
  line-height: 1.25;
}

body {
  min-height: 100vh;
  image-rendering: pixelated;
  background-image: url("Background.81ce682a.png");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  overflow-y: scroll;
}

body > span {
  text-align: center;
  margin-top: 1rem;
}

body > * {
  image-rendering: initial;
}

header {
  text-align: center;
  padding: 1em;
  font-size: 2.5rem;
}

footer {
  text-align: center;
  min-height: 15vh;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1em;
  padding: 1em;
  font-size: 1.25rem;
  display: flex;
}

footer > span {
  padding: .5em .75em;
}

img {
  -webkit-user-select: none;
  user-select: none;
}

dl {
  grid-template-columns: auto auto;
  justify-content: start;
  gap: 0 .5em;
  display: grid;
}

dd {
  margin: 0;
}

details > summary {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  padding: .5em .5em .25em;
  font-size: 1.5rem;
  list-style: none;
}

details > summary::marker, details > summary::-webkit-details-marker {
  display: none;
}

details > summary:before {
  margin-right: .5em;
}

details > summary:focus {
  outline-offset: calc(-1 * var(--border-thin));
}

details > div {
  flex-direction: column;
  row-gap: .5em;
  padding: .5em;
  display: flex;
}

details[open] {
  margin-bottom: .5em;
}

.professions > div > label > input, .file-input > div:first-child > label > input, .select-control > input[inputmode="none"], .checkbox > input {
  opacity: 0;
  position: absolute;
  transform: scale(0);
}

tr:nth-child(2n+1), #settings-load\/save > div:first-child > ul > li:nth-child(2n+1) {
  background-color: #f9ba66;
}

tr:nth-child(2n), #settings-load\/save > div:first-child > ul > li:nth-child(2n) {
  background-color: #fbcb8c;
}

.edit, .cancel, .ok {
  background-size: contain;
}

.ok {
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("Ok.03f98251.png");
}

.cancel {
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("Cancel.ac29e8d3.png");
}

.edit {
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("Edit.1902e2a8.png");
}

html {
  scroll-snap-type: y mandatory;
}

header {
  scroll-snap-align: start;
}

main {
  scroll-snap-align: start;
  height: 100vh;
  box-sizing: border-box;
  grid-template-columns: minmax(37.5rem, 1fr) minmax(42.5rem, 1fr);
  gap: 1rem;
  padding: 1rem;
  display: grid;
}

main > section {
  grid-template-rows: max-content;
  display: grid;
  overflow: hidden;
}

#graph {
  overflow: hidden;
}

footer {
  scroll-snap-align: end;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

dialog {
  min-width: 15rem;
  max-width: min(80ch, 40vw);
  flex-direction: column;
  align-items: center;
  row-gap: 1em;
  margin: auto;
  padding: 1rem;
  display: flex;
}

dialog > h1, dialog > p {
  margin: 0;
}

dialog > div {
  width: 100%;
  justify-content: space-around;
  align-items: center;
  column-gap: 1em;
  display: flex;
}

dialog > div:last-child {
  justify-content: space-between;
}

button {
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
}

input {
  font-family: inherit;
  font-size: inherit;
}

input[type="range"] {
  -webkit-appearance: none;
  cursor: pointer;
  width: max(100px, 10rem);
  height: calc(var(--border-thin) * 6 * 1.5);
  background: none;
  margin: 0;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  box-shadow: none;
  width: calc(var(--border-thin) * 10 * 1.5);
  height: calc(var(--border-thin) * 6 * 1.5);
  background: url("Range Thumb.97c73dca.png") 0 0 / contain;
  border: none;
  border-radius: 0;
}

input[type="range"]::-moz-range-thumb {
  box-shadow: none;
  width: calc(var(--border-thin) * 10 * 1.5);
  height: calc(var(--border-thin) * 6 * 1.5);
  background: url("Range Thumb.97c73dca.png") 0 0 / contain;
  border: none;
  border-radius: 0;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: calc(var(--border-thin) * 6 * 1.5);
  border-image-source: url("Range Track.fd64cd9c.png");
  border-image-slice: 22 fill;
  border-image-width: calc(var(--border-thin) * 2 * 1.5);
}

input[type="range"]::-moz-range-track {
  height: calc(var(--border-thin) * 6 * 1.5);
  border-image-source: url("Range Track.fd64cd9c.png");
  border-image-slice: 22 fill;
  border-image-width: calc(var(--border-thin) * 2 * 1.5);
}

[role="tablist"] {
  column-gap: .5em;
  padding-inline: .75em;
  display: flex;
}

[role="tab"] {
  min-width: 5em;
  outline-offset: 0;
  border-image-width: 1rem 1rem 0;
  border-image-outset: .75rem .75rem 0;
  margin: .75rem .75rem 0;
  padding: .75rem 1rem .5rem;
  font-size: 1.5rem;
}

[role="tab"][aria-selected="true"] {
  z-index: 1;
  transform: translateY(.5rem);
}

[role="tabpanel"] {
  overscroll-behavior: contain;
  overflow: auto;
}

[role="tabpanel"] > div {
  height: 100%;
  box-sizing: border-box;
  flex-direction: column;
  padding: 1em;
  display: flex;
}

@media (max-width: 83rem) {
  html {
    scroll-snap-type: none;
  }

  main {
    height: unset;
    min-width: min-content;
    grid-template-columns: 1fr;
  }

  main > section {
    min-height: max(37.5rem, 50vh - 1.5rem);
    overflow: unset;
  }

  [role="tabpanel"] {
    overflow: unset;
  }

  .visualization-graph {
    height: max(37.5rem, 50vh - 1.5rem);
  }

  .visualization-graph > [role="tabpanel"] {
    overflow: hidden;
  }
}

@media (max-width: 44.5rem) {
  :root {
    font-size: max(50%, 2vw);
  }

  body {
    --border-thin: 1px;
    --border-med: 2px;
  }
}

.label-hidden {
  -webkit-user-select: none;
  user-select: none;
  font-size: 0;
}

.label-text {
  margin-right: .5em;
}

.input-box {
  image-rendering: pixelated;
  -moz-appearance: textfield;
  background: none;
  border-image-source: url("Input Box.b544b1c3.png");
  border-image-slice: 18 18 12 12 fill;
  border-image-width: .5rem;
  padding: .625em .5em .5em;
}

.input-box::-webkit-outer-spin-button, .input-box::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
}

.checkbox > input {
  top: 0;
  left: 0;
}

.checkbox > input + img {
  content: url("Checkbox.76be4d9e.png");
  vertical-align: text-bottom;
  width: 1.5em;
  height: 1.5em;
}

.checkbox > input + img:not(:last-child) {
  margin-right: .5em;
}

.checkbox > input:checked + img {
  content: url("Checkedbox.92c930d7.png");
}

.checkbox > input:focus + img {
  outline-offset: 0;
}

.select {
  --border: #5b2b2a solid var(--border-med);
  cursor: pointer;
  display: inline-flex;
}

.select[aria-expanded="true"]:focus-within {
  outline: none;
}

.select[aria-expanded="false"]:hover > div > .select-control {
  background-color: wheat;
}

.select > div {
  position: relative;
}

.select > img {
  content: url("Select Spinner.875862f3.png");
  height: 2rem;
  border: var(--border);
  border-left: none;
}

.select-control {
  height: 100%;
  box-sizing: border-box;
  border: var(--border);
  background-color: #f9ba66;
  align-items: center;
  padding: 0 .5rem;
  display: flex;
  position: relative;
}

.select-control > input[type="search"] {
  width: calc(100% - 1rem);
  height: 100%;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  position: absolute;
}

.select-control > input[type="search"]:not(:placeholder-shown) + * {
  visibility: hidden;
}

.select-control > span {
  margin-top: .125rem;
}

.select-control span {
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline;
  overflow: hidden;
}

.select-list {
  max-height: 50vh;
  width: 100%;
  z-index: 2;
  border: var(--border);
  box-sizing: border-box;
  background-color: #f9ba66;
  border-top: none;
  position: absolute;
  top: 100%;
  left: 0;
  overflow: auto;
}

.select-list > li {
  padding: .5rem;
}

.select-list > li.empty {
  text-align: center;
  padding: .75rem;
}

.select-list > li[aria-selected="true"] {
  background-color: wheat;
}

.button, .file-input > div:first-child > label {
  color: #000;
  image-rendering: pixelated;
  background: none;
  border-image-source: url("Button.0b5d963a.png");
  border-image-slice: 14 fill;
  border-image-width: .25em;
  padding: .625em .5em .5em;
}

.preset {
  justify-content: space-between;
  align-items: baseline;
  column-gap: 1em;
  display: flex;
}

.preset > span:first-child {
  font-size: 1.25rem;
}

.file-input {
  display: block;
}

.file-input > div:first-child {
  align-items: center;
  column-gap: 1em;
  display: flex;
}

.file-input > div:first-child > label {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  margin-bottom: .5em;
  display: inline-block;
}

.file-input > div:last-child {
  border: black solid var(--border-thin);
  height: 10rem;
  background: #d3d3d3;
  justify-content: center;
  align-items: center;
  display: flex;
}

.professions {
  width: max-content;
  grid-template-columns: repeat(2, 1fr);
  gap: .5em;
  display: grid;
}

.professions > div {
  align-items: center;
  display: flex;
}

.professions > div:first-child, .professions > div:last-child:nth-child(2) {
  grid-row: span 2;
}

.professions > div > label {
  width: 100%;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  padding: .5em;
}

.professions > div > label > input:checked + * {
  color: green;
}

.professions > div > label:hover {
  background: #fff;
}

.professions > div > label:focus-within {
  outline-offset: 0;
}

.skill {
  width: max-content;
  flex-direction: column;
  row-gap: 1em;
  padding: .5em;
  display: flex;
}

.skill > span:first-child {
  width: 100%;
  text-align: center;
  font-size: 2rem;
}

.skill-level {
  flex-wrap: wrap;
  gap: 1em 2em;
  display: flex;
}

.number-and-range {
  align-items: center;
  column-gap: .5em;
  display: flex;
}

.number-and-range > label {
  align-items: center;
  display: flex;
}

.seasons {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.calendar {
  flex-direction: column;
  align-items: center;
  row-gap: 2em;
  display: flex;
}

.calendar-season {
  padding: var(--border-thin);
  min-width: min-content;
}

.calendar-header {
  justify-content: center;
  align-items: center;
  margin-bottom: .25em;
  font-size: 2rem;
  display: flex;
}

.calendar-days {
  gap: var(--border-thin);
  grid-template-columns: repeat(7, minmax(4rem, 7rem));
  grid-auto-rows: 1fr;
  display: grid;
}

.calendar-days > div {
  aspect-ratio: 1;
  outline: var(--border-thin) solid black;
  justify-content: center;
  align-items: center;
  padding: 10%;
  display: flex;
  position: relative;
}

.calendar-days > div > img {
  width: 50%;
  height: unset;
}

.calendar-days > div:nth-child(28n+1) {
  counter-reset: day;
}

.calendar-days > div:before {
  counter-increment: day;
  content: counter(day);
  position: absolute;
  top: 5%;
  left: calc(5% + .125rem);
}

#graph {
  min-height: 0;
  flex-grow: 1;
}

#graph-controls {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1em 2em;
  display: flex;
}

#graph-controls > div, #graph-controls > div > div {
  flex-wrap: wrap;
  align-items: center;
  gap: 1em .5em;
  display: flex;
