  row-gap: 1em;
  margin-block: 1em;
  display: flex;
}

#summary-controls > div {
  width: 100%;
  justify-content: center;
  align-items: center;
  column-gap: .5em;
  display: flex;
}

.edit {
  float: right;
}

.back {
  padding: .25em .25em .125em;
  font-size: 1.5rem;
}

.back:before {
  margin-right: .5em;
}

.settings-group {
